"use client"

import React, { useState } from 'react'
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { SubscriptionLimit } from "./tariff-and-subscription"

interface FormData {
  email: string
  password: string
  confirmPassword?: string
  name?: string
}

export function AuthForm() {
  const [isSignUp, setIsSignUp] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState("")
  const [showSubscriptionLimit, setShowSubscriptionLimit] = useState(false)
  const [formData, setFormData] = useState<FormData>({
    email: "",
    password: "",
    confirmPassword: "",
    name: ""
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  const validateInputs = () => {
    if (!formData.email.match(/^\S+@\S+\.\S+$/)) {
      return "Please enter a valid email address."
    }
    if (isSignUp && formData.password !== formData.confirmPassword) {
      return "Passwords do not match."
    }
    if (formData.password.length < 6) {
      return "Password must be at least 6 characters long."
    }
    return null
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")

    // Validate required fields
    if (!formData.email || !formData.password || (isSignUp && (!formData.name || !formData.confirmPassword))) {
      setError("Please fill in all required fields")
      return
    }
    
    const validationError = validateInputs()
    if (validationError) {
      setError(validationError)
      return
    }

    setIsLoading(true)
    try {
      // Simulate API call with subscription check
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      // Simulate user count check (50% chance of reaching limit)
      const currentUserCount = Math.random() > 0.5 ? 1000 : 500
      const userLimit = 750
      
      if (currentUserCount >= userLimit) {
        setShowSubscriptionLimit(true)
        setIsLoading(false)
        return
      }
      
      // Simulate successful authentication
      console.log("Form submitted:", {
        ...formData,
        type: isSignUp ? "signup" : "signin"
      })
      
      // Show success message (in real app, redirect to dashboard)
      setError("")
      alert(isSignUp ? "Account created successfully!" : "Signed in successfully!")
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
    } finally {
      setIsLoading(false)
    }
  }

  const handlePaymentSuccess = () => {
    setShowSubscriptionLimit(false)
    // In real app, update user's subscription status and proceed with sign in
    console.log("Payment successful, proceeding with authentication")
  }

  if (showSubscriptionLimit) {
    return (
      <SubscriptionLimit
        onClose={() => setShowSubscriptionLimit(false)}
        onPaymentSuccess={handlePaymentSuccess}
      />
    )
  }

  return (
    <Card className="w-full max-w-md mx-auto">
      <CardHeader className="space-y-1">
        <CardTitle className="text-2xl font-bold text-center">
          {isSignUp ? "Create an Account" : "Welcome Back"}
        </CardTitle>
        <CardDescription className="text-center">
          {isSignUp 
            ? "Enter your details to create your account" 
            : "Enter your credentials to sign in"}
        </CardDescription>
      </CardHeader>
      <CardContent>
        {error && (
          <Alert variant="destructive" className="mb-4">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}
        <form onSubmit={handleSubmit} className="space-y-4">
          {isSignUp && (
            <div className="space-y-2">
              <Label htmlFor="name">Full Name</Label>
              <Input
                id="name"
                type="text"
                name="name"
                placeholder="John Doe"
                value={formData.name}
                onChange={handleChange}
                required={isSignUp}
                className="w-full"
              />
            </div>
          )}
          <div className="space-y-2">
            <Label htmlFor="email">Email</Label>
            <Input
              id="email"
              type="email"
              name="email"
              placeholder="you@example.com"
              value={formData.email}
              onChange={handleChange}
              required
              className="w-full"
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="password">Password</Label>
            <Input
              id="password"
              type="password"
              name="password"
              placeholder="••••••••"
              value={formData.password}
              onChange={handleChange}
              required
              className="w-full"
            />
          </div>
          {isSignUp && (
            <div className="space-y-2">
              <Label htmlFor="confirmPassword">Confirm Password</Label>
              <Input
                id="confirmPassword"
                type="password"
                name="confirmPassword"
                placeholder="••••••••"
                value={formData.confirmPassword}
                onChange={handleChange}
                required={isSignUp}
                className="w-full"
              />
            </div>
          )}
          <Button 
            type="submit" 
            className="w-full"
            disabled={isLoading}
          >
            {isLoading 
              ? "Processing..." 
              : isSignUp 
                ? "Create Account" 
                : "Sign In"
            }
          </Button>
        </form>
        <div className="mt-4 text-center">
          <button
            type="button"
            onClick={() => setIsSignUp(!isSignUp)}
            className="text-sm text-gray-500 hover:text-gray-900 transition-colors"
          >
            {isSignUp 
              ? "Already have an account? Sign in" 
              : "Need an account? Sign up"}
          </button>
        </div>
      </CardContent>
    </Card>
  )
}
